import React, { Component } from 'react';
export default class Header extends Component {
  render() {
    let resumeData = this.props.resumeData;
    return (
      <React.Fragment>
       {/* Resume Section
   ================================================== */}
   <section id="resume">
        {/* Education
      ----------------------------------------------- */}
        <div className="row education">
          <div className="three columns header-col">
            <h1><span>Education</span></h1>
          </div>
          <div className="nine columns main-col">
            <div className="row item">
              <div className="twelve columns">
                <h3>{resumeData.u1}</h3>
                <p className="info">{resumeData.major}<span>•</span> <em className="date">{resumeData.y1}</em></p>
                <p>
                    {resumeData.desc1}
                </p>
              </div>
            </div> {/* item end */}
            <div className="row item">
              <div className="twelve columns">
                <h3>{resumeData.u2}</h3>
                <p className="info">{resumeData.major2}<span>•</span> <em className="date">{resumeData.y2}</em></p>
                <p>
                  {resumeData.desc2}
                </p>
              </div>
            </div> {/* item end */}
            <div className="row item">
              <div className="twelve columns">
                <h3>{resumeData.u3}</h3>
                <p className="info">{resumeData.major3}<span>•</span> <em className="date">{resumeData.y3}</em></p>
                <p>
                  
                </p>
              </div>
            </div> {/* item end */}
            <div className="row item">
              <div className="twelve columns">
                <h3>{resumeData.u4}</h3>
                <p className="info">{resumeData.major3}<span>•</span> <em className="date">{resumeData.y4}</em></p>
                <p>
                  
                </p>
              </div>
            </div> {/* item end */}
          </div> {/* main-col end */}
        </div> {/* End Education */}
        {/* Work
      ----------------------------------------------- */}
        <div className="row work">
          <div className="three columns header-col">
            <h1><span>Experience</span></h1>
          </div>
          <div className="nine columns main-col">
            <div className="row item">
              <div className="twelve columns">
                <h3>{resumeData.exp2}</h3>
                <p className="info">{resumeData.job1}<span>•</span> <em className="date">2014 (4 Months)</em></p>
                <p>
                  {resumeData.expdesc2}
                </p>
              </div>
            </div> {/* item end */}
            <div className="row item">
              <div className="twelve columns">
                <h3>{resumeData.exp3}</h3>
                <p className="info">{resumeData.job2}<span>•</span> <em className="date">2018 (4 Months)</em></p>
                <p>
                  {resumeData.expdesc3}
                </p>
              </div>
            </div> {/* item end */}
          </div> {/* main-col end */}
        </div> {/* End Work */}
        {/* Skills
      ----------------------------------------------- */}
      <div className="row work">
          <div className="three columns header-col">
            <h1><span>Achievements</span></h1>
          </div>
          <div className="nine columns main-col">
          <div className="row item">
              <div className="twelve columns">
                <h3>{resumeData.exp1}</h3>
                <p className="info">{resumeData.u2}<span>•</span> <em className="date">2014</em></p>
                <p>
                  {resumeData.expdesc1}
                </p>
              </div>
            </div> {/* item end */}
            <div className="row item">
              <div className="twelve columns">
                <h3>{resumeData.ac1}</h3>
                <p className="info">{resumeData.cert1}<span>•</span> <em className="date">{resumeData.ac_yr1}</em></p>
                <p>
                  {resumeData.ac_desc1}
                </p>
              </div>
            </div> {/* item end */}
          </div> {/* main-col end */}
        </div> {/* End Work */}
        <div className="row skill">
          <div className="three columns header-col">
            <h1><span>Skills</span></h1>
          </div>
          <div className="nine columns main-col">
            <p>{resumeData.skilldesc}
            </p>
            <div className="bars">
              <ul className="skills">
                <li><span className="bar-expand photoshop" /><em>Design</em></li>
                <li><span className="bar-expand illustrator" /><em>Web Developing</em></li>
                <li><span className="bar-expand wordpress" /><em>Cloud Computing</em></li>
                <li><span className="bar-expand css" /><em>Gaming</em></li>
                <li><span className="bar-expand html5" /><em>Networking</em></li>
                <li><span className="bar-expand jquery" /><em>Vulnerability</em></li>
              </ul>
            </div>{/* end skill-bars */}
          </div> {/* main-col end */}
        </div> {/* End skills */}
      </section> {/* Resume Section End*/}
      </React.Fragment>
    );
  }
}