import React, { Component } from 'react';
export default class Header extends Component {
  render() {
    let contactData = this.props.contactData;
    return (
      <React.Fragment>
      <section id="contact">
        <div className="row section-head">
          <div className="two columns header-col">
            <h1><span>Get In Touch.</span></h1>
          </div>
          <div className="ten columns">
            <p className="lead">
              {contactData.name}<br/>
              {contactData.address}<br/>
              {contactData.phone}<br/>
              <a href="https://www.linkedin.com/in/herbowo-prasetyo-008534108/">LinkedIn</a><br/>
              <a href="mailto:herbowoprasetyo@gmail.com">herbowoprasetyo@gmail.com</a><br/>
            </p>
          </div>
        </div>
      </section> {/* Contact Section End*/} 
      </React.Fragment>
    );
  }
}