import React, { Component } from 'react';
export default class Header extends Component {
  render() {
    let aboutData = this.props.aboutData;
    return (
      <React.Fragment>
      <section id="about">
        <div className="row">
          <div className="three columns">
            <img className="profile-pic" src="images/profilepic.jpg" alt="pasfoto"/>
          </div>
          <div className="nine columns main-col">
            <h2>About Me</h2>
            <p>{aboutData.desc}
            </p>
            <div className="row">
              <div className="columns contact-details">
                <h2>Contact Details</h2>
                <p className="address">
                  <span>{aboutData.name}</span><br />
                  <span>{aboutData.address}<br />
                  </span>
                  <span>{aboutData.phone}</span><br />
                  <a href="https://www.linkedin.com/in/herbowo-prasetyo-008534108/">LinkedIn</a><br/>
                  <span><a href="mailto:herbowoprasetyo@gmail.com">herbowoprasetyo@gmail.com</a><br/><br/><br/></span>
                </p>
              </div>
            </div> {/* end row */}
          </div> {/* end .main-col */}
        </div>
      </section> {/* About Section End*/}
      </React.Fragment>
    );
  }
}